import debounce from 'lodash/debounce'

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Filters',

  props: {
    options: {
      type: Object,
      default: () => {}
    },
    hasFilters: {
      type: Boolean,
      default: true
    },
    alwaysOpen: {
      type: Boolean,
      default: false
    },
    hasSearch: {
      type: Boolean,
      default: true
    }
  },

  data () {
    return {
      open: this.alwaysOpen,
      filters: {
        search: null
      }
    }
  },

  watch: {
    options: {
      deep: true,
      handler () {
        this.setFilters(this.options)
        this.$emit('filterChanged')
      }
    },

    'filters.search' () {
      this.debouncedSearch({ search: this.filters.search })
      this.$emit('debouncedSearch', this.filters.search)
    }
  },

  created () {
    this.debouncedSearch = debounce(this.search, 500)
  },

  methods: {
    toggleFilterMenu () {
      this.open = !this.open
    },
    search (value) {
      this.$store.commit('filters/clear')
      this.$store.commit('pagination/setPage', 1)
      this.setFilter(value)
    },
    setFilters (filters) {
      this.$store.commit('filters/addMany', filters)
      this.$store.commit('pagination/setPage', 1)
      this.$emit('filterChanged')
    },
    setFilter (value) {
      this.$store.commit('filters/addOne', value)
      this.$emit('filterChanged')
    },
    clearFilters () {
      this.$store.commit('filters/clear')
      this.toggleFilterMenu()
      this.$emit('filterChanged')
    }
  }
}
